header {
    height: 10vh;
    line-height: 5vh;
    background-image: linear-gradient(to right bottom, rgba(0, 0, 0, .9), rgba(0, 0, 0, .9), rgba(0, 0, 0, .6), rgba(0, 0, 0, .5 ), rgba(0, 0, 0));
    color: #fff;
     position: relative;
     z-index: 999;
}
.logo img {
    width: 150px; 
    margin-top: 20px;
    margin-right: 30px;
}
header a {
    color: #fff;
    margin-left: 30px;
}



  header i {
    quotes: 20px;
    height: 40px;
    padding: 10px;
    text-align: center;
    border-radius: 50%;
    border: 2px solid #fff;
    margin: 0 10px;
  }
  header button {
    border-radius: 5px;
    font-size: 15px;
     font-weight: bold;
  }
  .toggle {
    display: none;
  }
  
  .control-btn button {
   background: none;
   padding: 0;
   width: 50px;
   height: 50px;
   border-radius: 50%;
   cursor: pointer;
   border: 2px solid rgba(255, 255, 255, .3);
  }


  /* -- responsive style */
  @media only screen and (max-width:768px) {

    header nav .flex1 {
      display: none;
    }
    .icon-account {
      display: none;
    }
    .navMenu-list {
      display: flex;
      flex-direction: column;
      position: absolute;
      top: 0;
      left: 0;
      background-color: #eb5e28;
      width: 50%; 
      height: 100vh;
      z-index: 999;
    }
    .link-account {
      margin-top: 30px;
      padding: 0 50px;
    }
   
    .toggle{
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 20px; 
      margin: auto;
      cursor: pointer;
      background: none;
    }  
  }
