.login-container {
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 30px;
    background-color: #fff;
    padding-bottom: 30px;
    width: 600px;
    height: 600px;

}
.login {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;   
 
}
.login-title {
    color: #eb5e28;
    font-size: 48px;
    font-weight: 700;
}
.underline {
    width: 61px;
    height: 6px;
    background-color: #eb5e28;
    border-radius: 2rem;
}
.login-inputs {
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}
.login-input {
    display: flex;
    align-items: center;
    margin: auto;
    width: 480px;
    height: 80px;
    background-color: #ced4da;
    border-radius: 2rem;
}
.input-img {
    margin: 0 30px;
}
.login-input input {
    height: 50px;
    width: 400px;
    background: transparent;
    border: none;
    outline: none;
    color: #495057;
    font-size: 19px;
}
.forgot-password {
    padding-left: 62px;
    margin-top: 27px;
    color: #495057;
    font-size: 18px;
}
.forgot-password .click-here {
    color: red;
    cursor: pointer;
}
.submit-container {
    display: flex;
    gap: 30px;
    margin: 60px auto;
}
.submit {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 59px;
    color: #fff;
    background-color: #eb5e28;
    border-radius: 2rem;
    font-size: 18px;
    font-weight: 700;
    cursor: pointer;
}
.gray {
    color:  #eb5e28;
    background-color: #495057;
}

/* -- responsive style */
@media only screen and (max-width: 768px) {
    .login-container {
        width: 400px;
        height: 400px;

    }
    .login {
        width: 50%;
    }
    .login-title {
        font-size: 25px;
    }

    .login-input {
        width: 350px;
        height: 50px;
    }
    .forgot-password {
        font-size: 12px;
    }
    .submit {
        width: 90px;
        height: 39px;
        margin-bottom: 90px;
        font-size: 15px;
    }
}